}

.image {
    display: block;
    margin-left: auto;
    margin-right: auto;
}

.end-quote {
    text-align: center;
    font-family: 'Reem Kufi';
    font-style: italic;
}

.code {
    margin-top: 3%;
    margin-bottom: 3%;
